
#small-chat {
    position: fixed;
    bottom: 50px;
    right: 26px;
    z-index: 999;
    cursor: pointer;
}
#small-chat .badge {
    position: absolute;
    top: -3px;
    right: -4px;
}
.rotate45{
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
}
.open-small-chat {
    height: 38px;
    width: 38px;
    display: block;
    background: #1ab394;
    padding: 9px 8px;
    text-align: center;
    color: #fff;
    border-radius: 50%;
}
.open-small-chat:hover {
    color: white;
    background: #1ab394;
}
.small-chat-box {
    position: fixed;
    bottom: 50px;
    right: 80px;
    background: #fff;
    border: 1px solid #e7eaec;
    width: 230px;
    height: 320px;
    border-radius: 4px;
    z-index: 999;
}
.small-chat-box.ng-small-chat {
    display: block;
}
.body-small .small-chat-box {
    bottom: 70px;
    right: 20px;
}
.small-chat-box.active {
    display: block;
}
.small-chat-box .heading {
    background: #2f4050;
    padding: 8px 15px;
    font-weight: bold;
    color: #fff;
}
.small-chat-box .chat-date {
    opacity: 0.6;
    font-size: 10px;
    font-weight: normal;
}
.small-chat-box .content {
    width: auto;
    height: 234px;
    background-color: #fff;
    padding: 15px 15px;
    overflow-y: scroll;
}
.small-chat-box .content::-webkit-scrollbar{
    width: 5px;
    background-color: #f5f5f5;
}
.small-chat-box .content::-webkit-scrollbar-thumb{
    background-color: #1ab394;
}
.small-chat-box .content .author-name {
    font-weight: bold;
    margin-bottom: 3px;
    font-size: 11px;
}
.small-chat-box .content > div {
    padding-bottom: 20px;
}
.small-chat-box .content .chat-message {
    padding: 5px 10px;
    border-radius: 6px;
    font-size: 11px;
    line-height: 14px;
    max-width: 80%;
    background: #f3f3f4;
    margin-bottom: 10px;
}
.small-chat-box .content .chat-message.active {
    background: #1ab394;
    color: #fff;
}
.small-chat-box .content .left {
    text-align: left;
    clear: both;
}
.small-chat-box .content .left .chat-message {
    float: left;
}
.small-chat-box .content .right {
    text-align: right;
    clear: both;
}
.small-chat-box .content .right .chat-message {
    float: right;
}
.small-chat-box .form-chat {
    padding: 10px 10px;
}
.small-chat-box .form-chat .form-control {
    text-align: left;
    width: 135px;
}
.small-chat-box .form-chat .myGreen-btn{
    color: white;
    background-color: #1ab394;
}
.small-chat-box .form-chat .myGreen-btn:hover{
    color: whitesmoke;
}



